<template>
  <button class="close"><span /></button>
</template>

<script>

export default {
  name: 'Close',
};
</script>

<style lang='scss'>

.close{
  background: #ffffff;
  border: none;
  padding: 2px;
}

.close>span {
  position: relative;
  width: 16px;
  height: 16px;
  opacity: 0.5;
  float: right;
}

.close>span:hover {
  opacity: 1;
}

.close>span:before, .close>span:after {
  position: absolute;
  top: 2px;
  left: 7px;
  content: ' ';
  height: 12px;
  width: 2px;
  background-color: #333333;
}

.close>span:before {
  transform: rotate(45deg);
}

.close>span:after {
  transform: rotate(-45deg);
}
</style>
