<template>
  <div id="app">
    <div class="content">
      <Button @click.native='windowAdd=true'>Add</Button>
      <Table
        :headers='true'
        :columns='columns'
        :data='people'
        @sortby='sortBy'
        :sortOrder='sortOrder'
        :sortKey='sortKey'
        @click-to-row='toggleNestedTable'
      >
        <template v-for='id in openNests' v-slot:[id]>
          <Table 
            :columns='columns'
            :data='makeDataToNestedTable(id)'
            class='app__table-nested'
          >
          </Table>
        </template>
      </Table>
    </div>
    <Window v-if='windowAdd' title='Add new man' @close='windowAdd=false'>
      <AddForm :people='people' @submit='addMan'></AddForm>
    </Window>
  </div>
</template>

<script>
import Table from './components/Table.vue';
import Button from './components/Button.vue';
import Window from './components/Window.vue';
import AddForm from './components/AddForm.vue';

export default {
  name: 'App',
  components: {
    Table,
    Button,
    Window,
    AddForm,
  },
  mounted() {
    // Для записи данных при перезагрузке я использовал event unload. 
    // Но он срабатывает не только при перезагрузке, переход 
    // или закрытие страницы так же его вызывают. 
    // Способа узнать, что будет именно перезагрузка я не нашел. 
    window.addEventListener('unload', () => {
      this.savePeopleToLocalStorage();
    });

    // получение данных  из localStorage
    let people = window.localStorage.getItem('people');
    let idCounter = window.localStorage.getItem('idCounter'); 
    if (people !== null || idCounter !== null) {
      this.people = JSON.parse(people);
      this.idCounter = parseInt(idCounter);
    }
  },
  data() {
    return {
      people: [],
      windowAdd: false,
      columns: [
        'firstName',
        'lastName',
        'phone',
      ],
      idCounter: 0,
      sortOrder: 0,
      sortKey: '',
      openNests: [
      ],
    };
  },
  methods: {
    addMan(man) {
      this.people.push({
        id: '' + this.idCounter++,
        ...man,
      });
      this.windowAdd = false;
    },
    savePeopleToLocalStorage() {
      let people = JSON.stringify(this.people);
      window.localStorage.setItem('people', people);
      window.localStorage.setItem('idCounter', this.idCounter);
    },
    sortBy(event) {
      let sortOrder = event.order;
      let key = event.key;

      this.sortOrder = event.order;
      this.sortKey = event.key;

      this.people.sort((firstMan, secondMan) => {
        if (firstMan[key] < secondMan[key]) {
          return -1 * sortOrder;
        }
        if (firstMan[key] > secondMan[key]) {
          return 1 * sortOrder;
        }
        return 0;
      });
    },
    getById(array, id) {
      let result = array.find((item) => {
        if (id === item.id) {
          return true;
        }
      });
      return result;
    },
    getParentById(id) {
      let man = this.getById(this.people, id);
      let parent =  this.getById(this.people, man.parentId);
      if (!parent) {
        return
      }
      return parent;
    },
    makeDataToNestedTable(id) {
      let parent = this.getParentById(id);
      if (!parent) {
        return [];
      }
      return [ 
        parent
      ];
    },
    toggleNestedTable(id) {
      let nestedIndex = this.openNests.findIndex((value) => {
        if (value === id) {
          return true
        }
      })
      // delete nested index if is already exists
      if (nestedIndex >= 0) {
        this.openNests = [
          ...this.openNests.slice(0, nestedIndex),
          ...this.openNests.slice(nestedIndex + 1)
        ]
        return;
      }

      this.openNests.push(id)
    }
  },
};


</script>

<style lang='scss'>

body{
  margin: 0px;
}

*{
  box-sizing: border-box;
}

.app__table-nested{
  width: 95%;
  margin-left: 5%;
}
.content{
  width: 70% ;
  margin: 0px auto;
}
</style>
