<template>
  <table class="table">
    <thead v-if='headers'>
      <tr class="table__header-row" v-if='columns'>
        <th 
          v-for="key in columns"
          @click="sortBy(key)"
          :class="{ active: sortKey == key }"
          :key='key'
          class='table__header-column'
        >
          {{ key | createColumnName }}
          <span class="table__pointer" :class="getPointerModificator(key)">
          </span>
        </th>
      </tr>
    </thead>
    <tbody class="table__body">
      <template v-for="item in data">
        <tr class="table__row"  :key='item.id' @click='$emit("click-to-row", item.id)'>
          <td class='table__column' v-for="key in columns" :key='key'>
            {{ item[key] }}
          </td>
          <slot :name='item.id'>
          </slot>
        </tr>
      </template>
    </tbody>
  </table>
</template>

<script>
export default {
  name: 'Table',
  props: [
    'columns',
    'data',
    'sortOrder',
    'sortKey',
    'open-nests',
    'headers',
  ],
  methods: {
    getPointerModificator(key) {
      if (this.sortOrder < 0 && this.sortKey === key) {
        return 'table__pointer_up';
      } else if (this.sortOrder > 0 && this.sortKey === key) {
        return 'table__pointer_down';
      }
    },
    sortBy(key) {
      let order = this.sortOrder * -1 || 1;
      if (key !== this.sortKey) {
        order = 1;
      }
      this.$emit('sortby', { key, order });
    },
  },
  filters: {
    createColumnName(column) {
      if (!column) return '';
      let columnName = column.toString();
      columnName = columnName.charAt(0).toUpperCase() + columnName.slice(1);

      columnName = columnName.split('').map((symbol, index) => {
        if ( index === 0) {
          return symbol;
        }

        if(checkUpperCase(symbol)){
          return ' ' + symbol;
        }
        return symbol;
      });

      return columnName.join('');
    },
  },
};

function checkUpperCase(symbol) {
  return symbol.toUpperCase() === symbol;
}

</script>

<style lang='scss'>

.table{
  width: 100%;
  overflow: hidden;
}

.table__column{
  flex: 1;
  padding: 5px;
  text-align: center;
  overflow: hidden;
  border: 1px solid rgba(0, 0, 0, 0.17);
  display: block;
}

.table__header-column{
  text-align: center;
  flex: 1;
  display: block;
  border: 2px solid rgba(0, 0, 0, 0.8);
}

.table__header-row{
  display: flex;
  border: 2px solid rgba(0, 0, 0, 0.8);
}

.table__row{
  flex-wrap: wrap;
  display: flex;
  border: 2px solid rgba(0, 0, 0, 0.17);
}

.table__body{
  background: #fff;
}

.table__pointer {
  display: inline-block;
  vertical-align: middle;
  width: 5px;
  height: 5px;
  margin-left: 5px;
  opacity: 0.66;
  border-radius: 50%;
  background: rgba(0, 0, 0, 0.8);
}

.table__pointer_up {
  width: 0;
  height: 0;
  border: none;
  border-left: 4px solid transparent;
  border-right: 4px solid transparent;
  border-bottom: 4px solid rgba(0, 0, 0, 0.8);
  background: transparent;
}

.table__pointer_down {
  width: 0;
  height: 0;
  border: none;
  border-left: 4px solid transparent;
  border-right: 4px solid transparent;
  border-top: 4px solid rgba(0, 0, 0, 0.8);
  background: transparent;
}

</style>
