<template>
  <form @submit.prevent='$emit("submit", data)'>
    <label>
      First name:
      <input class="add-form__input" type='text' v-model='data.firstName' name='firstName' />
    </label>
    <label>
      Last: name:
      <input class='add-form__input' type='text' v-model='data.lastName' name='lastName'/>
    </label>
    <label>
      Phone:
      <input class="add-form__input" type='text' v-model='data.phone' name='phone'/>
    </label>  
    <select class='add-form__select' name='parentId' v-model='data.parentId'>
      <option disabled value="">Select parent</option>
      <option v-for="item in people" :key='item.id' :value="item.id">
        {{ item.firstName + ' ' + item.lastName }}
      </option>
    </select>
    <input class='add-form__submit' type="submit" value="Add" />
  </form>
</template>

<script>
export default {
  name: 'AddForm',
  props: [
    'people',
  ],
  data() {
    return {
      data: {
        firstName: '',
        lastName: '',
        phone: '',
        parentId: '',
      },
    };
  },
};
</script>

<style lang='scss'>

.add-form__submit{
  margin: 5px 0;
  font-size: 20px;
  display: block;
  border: 1px solid rgba(0, 0, 0, 0.17);
  height: 30px;
  width: 100px;
  padding: 3px;
  background: transparent;
}

.add-form__select {
  margin: 10px 0;
  width: 200px;
  height: 30px;
  border: 1px solid rgba(0, 0, 0, 0.17)
}

.add-form__input{
  margin: 10px 0;
  font-size: 20px;
  display: block;
  border: 1px solid rgba(0, 0, 0, 0.17);
  height: 30px;
  width: 200px;
  padding: 3px;
}

</style>
